<template>
  <div>
    <Stats :stat_info="statInfo" @myEvent="tableStats" />
    <CRow>
      <CCol md="12">
        <CCard class="clapans">
          <CCardHeader>
            <div class="d-flex aligin-items-center justify-content-between">
              <span>Мастер Хабы</span>
              <div>
                <CButton color="success" class="mr-2" @click="downloadArray">
                  Скачать историю Мастер Хаба в csv
                </CButton>
              </div>
            </div>
          </CCardHeader>
          <CCardBody>
            <div class="d-flex aligin-items-center justify-content-between">
              <CSelect
                v-if="isSelect"
                horizontal
                label="Действие на выбор"
                :value.sync="action_switcher"
                :options="[
                  'Не выбрано ничего',
                  'Включить отмеченные',
                  'Отключить отмеченные',
                ]"
                v-on:change="actionSwitcher()"
                class="col-4"
              />
              <CSelect
                horizontal
                label="Город"
                :options="cities_name"
                :value.sync="action_switcher_city"
                v-on:change="actionSwitcherCity()"
                class="col-4"
              />
            </div>
            <CDataTable
              class="mb-0 table-outline"
              hover
              :items="tableItems"
              :fields="tableFields"
              head-color="light"
              column-filter
              table-filter
              sorter
              :tableFilter="{
                label: 'Поиск',
                placeholder: 'Введите слово для поиска',
              }"
            >
              <template #select-filter="{ item }">
                <CInputCheckbox
                  :value.sync="all_select"
                  custom
                  inline
                  @change="allSelect()"
                />
              </template>
              <td slot="select" slot-scope="{ item, index }">
                <input
                  type="checkbox"
                  v-model="item.select"
                  custom
                  inline
                  @change="actionSelectChange(index)"
                />
              </td>
              <td slot="status" slot-scope="{ item, index }">
                <span
                  class="badge"
                  :class="'badge-' + colorStatus(item.status)"
                  >{{ textStatus(item.status) }}</span
                >
                <br />
                <CSwitch
                  class="mt-1"
                  color="primary"
                  :checked="item.status == 1"
                  :value="item.status"
                  @update:checked="actionStatusChange(item, index)"
                />
              </td>
              <td slot="city" slot-scope="{ item }">
                {{ cities_key[item.city] }}
              </td>
              <td slot="district" slot-scope="{ item }">
                {{ item.district }}
              </td>
              <td slot="street" slot-scope="{ item }">
                {{ item.street }}
              </td>
              <td slot="building" slot-scope="{ item }">
                {{ item.buildingname }}
              </td>
              <td slot="numHouse" slot-scope="{ item }">
                {{ item.buildingnum }}
              </td>
              <td slot="numHouse" slot-scope="{ item }">
                {{ item.entrancenum }}
              </td>
              <!-- <td slot="numHouse" slot-scope="{ item }">
                {{ item.repairday }}
              </td>
              <td slot="numHouse" slot-scope="{ item }">
                {{ item.repairtime }}
              </td> -->

              <td slot="settings" slot-scope="{ item }">
                <span
                  style="white-space: nowrap; cursor: pointer"
                  class="d-flex align-items-center"
                  @click="showSettingModal(item)"
                  ><CIcon name="cilSettings" class="mr-1" /> Настройки
                </span>
                <span
                  style="white-space: nowrap; cursor: pointer"
                  class="d-flex align-items-center"
                  @click="
                    $router.push({ path: '/master-hub/' + item.masterhubID })
                  "
                  ><CIcon name="cilSettings" class="mr-1" /> История
                </span>
              </td>
            </CDataTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
    <CModal title="Добавить новый Мастер Хаб" :show.sync="NewHubModal">
      <CCard>
        <CCardBody>
          <CForm v-on:submit="submitForm()">
            <CSelect
              horizontal
              label="Город"
              :value.sync="setting.city"
              :options="cities"
              required
            />
            <!-- <CInput type="text" v-model="setting.city" label="Свой вариант" horizontal
            /> -->
            <CInput
              type="text"
              v-model="setting.district"
              label="Район"
              horizontal
              required
            />
            <!-- <CSelect horizontal label="Улица" :value.sync="setting.street" :options="streets"/> -->
            <CInput
              type="text"
              v-model="setting.street"
              label="Улица"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.typeofbuilding"
              label="Тип здания"
              horizontal
              required
            />

            <CInput
              type="text"
              v-model="setting.buildingnum"
              label="Номер здания"
              horizontal
              required
            />

            <!-- <CSelect horizontal label="Подъезд" :value.sync="setting.entrancenum" :options="entrances"/> -->
            <CInput
              type="text"
              v-model="setting.entrancenum"
              label="Подъезд"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.buildingname"
              label="Название здания"
              horizontal
              required
            />
            <!-- <CSelect
              horizontal
              label="День недели ТО"
              :value.sync="setting.repairday"
              :options="entrances"
            />
            <CSelect
              horizontal
              label="Время ТО"
              :value.sync="setting.repairtime"
              :options="entrances"
            /> -->

            <CButton color="success" type="submit"> Добавить </CButton>
          </CForm>
        </CCardBody>
      </CCard>
      <div slot="footer"></div>
    </CModal>
    <CModal title="Настройки" :show.sync="SettingsModal">
      <CCard>
        <CCardBody>
          <CForm v-on:submit="editForm()">
            <CSelect
              horizontal
              label="Город"
              :value.sync="setting.city"
              :options="cities"
              required
            />
            <!-- <CInput type="text" v-model="setting.city" label="Свой вариант" horizontal
            /> -->
            <CInput
              type="text"
              v-model="setting.district"
              label="Район"
              horizontal
              required
            />
            <!-- <CSelect horizontal label="Улица" :value.sync="setting.street" :options="streets"/> -->
            <CInput
              type="text"
              v-model="setting.street"
              label="Улица"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.typeofbuilding"
              label="Тип здания"
              horizontal
              required
            />

            <CInput
              type="text"
              v-model="setting.buildingnum"
              label="Номер здания"
              horizontal
              required
            />

            <!-- <CSelect horizontal label="Подъезд" :value.sync="setting.entrancenum" :options="entrances"/> -->
            <CInput
              type="text"
              v-model="setting.entrancenum"
              label="Подъезд"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.buildingname"
              label="Название здания"
              horizontal
              required
            />
            <!-- <CSelect
              horizontal
              label="День недели ТО"
              :value.sync="setting.repairday"
              :options="entrances"
            />
            <CSelect
              horizontal
              label="Время ТО"
              :value.sync="setting.repairtime"
              :options="entrances"
            /> -->
            <CButton color="success" type="submit"> Изменить </CButton>
          </CForm>
        </CCardBody>
      </CCard>
      <div slot="footer"></div>
    </CModal>
    <div class="alert_block">
      <CAlert color="warning" closeButton v-if="warning">
        {{ alert_message }}
      </CAlert>
      <CAlert color="primary" closeButton v-if="success">
        {{ alert_message }}
      </CAlert>
    </div>
  </div>
</template>

<script>
import Stats from "./widgets/Stats";
import downloadCsv from "download-csv";

const qs = require("qs");
const axios = require("axios");
export default {
  name: "MasterHubs",
  components: {
    Stats,
  },
  data() {
    return {
      statInfo: [],
      cities_name: "",
      cities_arr: "",
      all_select: false,
      warning: false,
      success: false,
      alert_message: "",
      SettingsModal: false,
      NewHubModal: false,
      isSelect: false,
      action_switcher: "",
      action_switcher_city: "",
      id: "",
      tableItems: [],
      tableFields: [
        { key: "select", label: "Выбор" },
        { key: "status", label: "Статус" },
        { key: "masterhubID", label: "ID мастер хаба" },
        { key: "city", label: "Город " },
        { key: "district", label: "Район ", _classes: "text-center" },
        { key: "street", label: "Улица " },
        { key: "typeofbuilding", label: "Вид здания" },
        { key: "buildingnum", label: "Номер дома " },
        { key: "entrancenum", label: "Номер подъезда " },
        // { key: "settingday", label: "День настройки" },
        // { key: "settingtime", label: "Время настройки" },
        { key: "settings", label: "Действия" },
      ],
      setting: {},
      cities: [
        { value: null, label: "Выберите город" },
        { value: "1", label: "Алма-ата" },
        { value: "2", label: "Нур-Султан" },
        { value: "3", label: "Шымкент" },
        { value: "4", label: "Актобе" },
        { value: "5", label: "Караганда" },
        { value: "6", label: "Тараз" },
        { value: "7", label: "Павлодар" },
        { value: "8", label: "Усть-Каменогорск" },
        { value: "9", label: "Семей" },
        { value: "10", label: "Атырау" },
        { value: "11", label: "Костанай" },
        { value: "12", label: "Кызылорда" },
        { value: "13", label: "Уральск" },
        { value: "14", label: "Петропавловск" },
        { value: "15", label: "Темиртау" },
        { value: "16", label: "Актау" },
        { value: "17", label: "Туркестан" },
        { value: "18", label: "Кокшетау" },
        { value: "19", label: "Талдыкорган" },
        { value: "20", label: "Экибастуз" },
        { value: "21", label: "Рудный" },
      ],
      repairday: [
        { value: 1, label: "Пн" },
        { value: 2, label: "Вт" },
        { value: 3, label: "Ср" },
        { value: 4, label: "Чт" },
        { value: 5, label: "Пт" },
        { value: 6, label: "Сб" },
        { value: 7, label: "Вс" },
      ],
      repairtime: [
        { value: 1, label: "Пн" },
        { value: 2, label: "00:00 - 02:00" },
        { value: 3, label: "02:00 - 04:00" },
        { value: 4, label: "04:00 - 06:00" },
        { value: 5, label: "06:00 - 08:00" },
      ],
      districts: [
        { value: "Сарыаркинский", label: "Сарыаркинский" },
        { value: "Есиль", label: "Есиль" },
      ],
      streets: [
        { value: "Сарыаркинский", label: "Кабанбай батыра" },
        { value: "Есиль", label: "Туран" },
      ],
      buildings: [
        { value: "Сарыаркинский", label: "ЖК HighWill" },
        { value: "Есиль", label: "ЖК Инфинити" },
      ],
      houses: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      entrances: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      aptoroffices: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      shaftnums: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      floors: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      types: [
        { value: 1, label: "Физ лицо" },
        { value: 2, label: "Юр лицо" },
      ],
      payments: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
    };
  },
  mounted() {
    let city = auth.cities();
    this.cities_name = city.cities_name;
    this.cities_arr = city.cities;
    this.cities_key = city.cities_key;
    this.getResults(1);
    this.interval = setInterval(() => this.getResults(1), 30000);
  },
  methods: {
    tableStats(val){
      if (val === 1){
        this.tableItems = this.statInfo[1]
      }
      else if (val === 2){
        this.tableItems = this.statInfo[2]
      }
      else {
        this.tableItems = this.statInfo[4]
      }
    },
    showSettingModal(item) {
      console.log(qs.stringify(this.setting));
      this.setting = item;
      this.SettingsModal = !this.SettingsModal;
    },
    addNewHubModal() {
      this.setting = {};
      this.NewHubModal = !this.NewHubModal;
    },
    downloadArray() {
      const datas = this.tableItems;
      const columns = {
        select: "Выбор",
        status: "Статус клапана",
        city: "Город ",
        district: "Район ",
        street: "Улица ",
        typeofbuilding: "Вид здания",
        buildingnum: "Номер дома ",
        entrancenum: "Номер подъезда ",
        shaftnum: "Номер шахты ",
        floor: "Этаж",
        aptoroffice: "Кв/офис",
        valveID: "ID клапана",
        settings: "Действия",
      };
      const current = new Date();
      const date = `${current.getDate()}-${
        current.getMonth() + 1
      }-${current.getFullYear()}`;
      let name = "";
      if (this.action_switcher_city.length >= 1) {
        name =
          "Мастер хабы в городе" +
          this.action_switcher_city +
          " " +
          date +
          ".csv";
      } else {
        name = "Мастер хабы в городе" + " Алма-Ата " + date + ".csv";
      }
      downloadCsv(datas, columns, name);
    },
    allSelect() {
      let app = this;
      app.isSelect = true;
      for (var i = 0; i < app.tableItems.length; i++) {
        if (app.all_select == false) {
          this.tableItems[i].select = true;
          this.tableItems[i].street = this.tableItems[i].street + " ";
        } else {
          this.tableItems[i].select = false;
          this.tableItems[i].street = this.tableItems[i].street + " ";
        }
      }
      app.all_select = app.all_select == true ? false : true;
    },
    submitForm() {
      event.preventDefault();
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/addmasterhub/",
        data: qs.stringify(this.setting),
      })
        .then(({ data }) => {
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно добавлено";
          this.NewHubModal = false;
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
          this.getResults(1);
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    editForm() {
      event.preventDefault();
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/updatemasterhub/",
        data: qs.stringify(this.setting),
      })
        .then(({ data }) => {
          app.warningModal = false;
          app.success = true;
          console.log("SMTH");
          app.alert_message = "Успешно изменено";
          this.SettingsModal = false;
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
          this.getResults(1);
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    actionSelectChange(value) {
      let app = this;
      app.isSelect = true;
      if (app.tableItems[value].select == false) {
        app.tableItems[value].select = true;
        app.tableItems[value].street = this.tableItems[value].street + " ";
      } else {
        app.tableItems[value].select = false;
        app.tableItems[value].street = this.tableItems[value].street + " ";
      }
    },
    actionSwitcher() {
      let app = this;
      for (var i = 0; i < app.tableItems.length; i++) {
        if (app.tableItems[i].select == true) {
          if (this.action_switcher == "Включить отмеченные") {
            axios.defaults.headers.common["Authorization"] =
              "Bearer " + window.auth.token;
            axios({
              method: "post",
              url: "/turnon/",
              data: qs.stringify({
                device_id: app.tableItems[i].valveID,
              }),
            })
              .then(({ data }) => {
                app.success = true;
                app.alert_message = data.responce;
                setTimeout(function () {
                  app.success = false;
                }, 3000);
              })
              .catch(function (error) {});
          } else if (this.action_switcher == "Отключить отмеченные") {
            axios.defaults.headers.common["Authorization"] =
              "Bearer " + window.auth.token;
            axios({
              method: "post",
              url: "/turnoff/",
              data: qs.stringify({
                device_id: app.tableItems[i].valveID,
              }),
            })
              .then(({ data }) => {
                app.success = true;
                app.alert_message = data.responce;
                setTimeout(function () {
                  app.success = false;
                }, 3000);
              })
              .catch(function (error) {});
          } else {
          }
        }
      }
    },
    actionSwitcherCity() {
      this.getResults(this.cities_arr[this.action_switcher_city]);
    },
    actionStatusChange(item) {
      console.log(this.action_switcher_city);
      console.log(item);
      let app = this;
      var status =
        item.status == 1 || item.status == 2 || item.status == 0
          ? "/turnonmasterhub/"
          : "/turnoffmasterhub/";
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: status,
        data: qs.stringify({
          device_id: item.masterhubID,
        }),
      })
        .then(({ data }) => {
          app.success = true;
          app.alert_message = data.responce;
          setTimeout(function () {
            app.success = false;
          }, 3000);

          if (this.cities_arr[this.action_switcher_city] == undefined) {
            this.getResults(1);
          } else {
            this.getResults(this.cities_arr[this.action_switcher_city]);
          }
        })
        .catch(function (error) {});
    },
    colorStatus(value) {
      let color = "secondary";
      if (value == 1) {
        color = "secondary";
      } else if (value == 2 || value == 4) {
        color = "warning";
      } else if (value == 3) {
        color = "danger";
      } else if (value == 5) {
        color = "success";
      }
      return color;
    },
    textStatus(value) {
      var color = "Выключен";
      if (value == 1) {
        color = "Отключен";
      } else if (value == 2) {
        color = "Отключается";
      } else if (value == 3) {
        color = "Сломался";
      } else if (value == 4) {
        color = "Включается";
      } else if (value == 5) {
        color = "Включен";
      } else {
        color = "Выключен";
      }
      return color;
    },
    color(value) {
      let $color;
      if (value <= 25) {
        $color = "info";
      } else if (value > 25 && value <= 50) {
        $color = "success";
      } else if (value > 50 && value <= 75) {
        $color = "warning";
      } else if (value > 75 && value <= 100) {
        $color = "danger";
      }
      return $color;
    },
    getResults(city) {
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios
        .get("/getmasterhubbycity/" + city)
        .then((response) => {
          app.tableItems = response.data[0];
          this.statInfo = response.data;
          for (var i = 0; i < app.tableItems.length; i++) {
            app.tableItems[i].select = false;
          }
        })
        .catch((error) => {
          if (error.response.status == 401) {
            window.auth.logout();
          }
        });
    },
  },
};
</script>
<style>
.clapans .form-inline {
  margin-left: 15px;
}

.alert_block {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 100000;
}
</style>
